.App {
	font-family: sans-serif;
	margin: 0;
	height: 100%;
	scroll-behavior: smooth;
}

header {
	background-color: white;
	padding-bottom: 10px;
}

.Profile-Main-Logo {
	height: 500px;
	padding-top: 20px;
	margin-top: 30px;
}

.Profile-Name {
	font-size: 25px;
	font-weight: bolder;
	-webkit-animation-name: color-change; /* Safari 4.0 - 8.0 */
	-webkit-animation-duration: 2s; /* Safari 4.0 - 8.0 */
	animation-name: color-change;
	-webkit-animation-iteration-count: infinite;
	        animation-iteration-count: infinite;
	-webkit-animation-delay: 2s;
	        animation-delay: 2s;
}

@-webkit-keyframes color-change {
	0% {
		color: Black;
	}
	25% {
		color: DarkSlateGray;
	}
	50% {
		color: DimGrey;
	}
	100% {
		color: Gray;
	}
}

@keyframes color-change {
	0% {
		color: Black;
	}
	25% {
		color: DarkSlateGray;
	}
	50% {
		color: DimGrey;
	}
	100% {
		color: Gray;
	}
}

.App-logo {
	height: 200px;
	float: left;
	padding: 0 15px 15px 10px;
}

.About-Me-Content {
	font-size: 1.1rem;
	text-align: left;
	padding-right: 20px;
}

.App-intro {
	font-size: 30px;
	color: DarkSlateGray;
	font-weight: bolder;
	background-color: #ecf0f1;
	padding-top: 5%;
	padding-bottom: 5%;
}

.Section {
	padding-bottom: 50px;
	background-color: #ecf0f1;
	width: 98%;
	border-spacing: 20px;
	display: inline-block;
}
.Project-Work-Section {
	background-color: white;
	border-spacing: 20px;
}

.Projects-Section {
	margin-top: 1rem;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
}

.App-side {
	font-weight: bolder;
	color: DarkSlateGray;
	font-size: 1.2rem;
	padding-top: 40px;
	text-align: left;
	padding-left: 10px;
}

.App-side-content {
	color: black;
	padding-top: 40px;
	padding-left: 30px;
	width: 100%;
}

.Project-Title {
	background-color: #ecf0f1;
	color: DarkSlateGray;
}

.Project-image {
	height: 100%;
	width: 100%;
}

.Projects-Description {
	color: DarkSlateGray;
	display: grid;
	grid-template-columns: 3fr 4fr;
	margin: 1rem;
	padding: 1rem;
	opacity: 0.95;
	-webkit-box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
	        box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}
.Project-details {
	margin: 5rem;
	font-size: 1.1rem;
	text-align: left;
}

.Project-detailed-info {
	font-size: 1.2rem;
	text-align: left;
}

.Contact-info {
	font-size: 15px;
	background-color: #ecf0f1;
}

.Contact-options {
	padding-top: 15px;
	padding-bottom: 15px;
	opacity: 0.95;
	position: fixed;
	z-index: 100;
	-webkit-box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
	        box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
	background-color: #ecf0f1;
	width: 100%;
}

.Other-Links:link,
.Other-Links:visited,
.Other-Links:active {
	color: DarkSlateGray;
	text-decoration: none;
	text-align: center;
	padding: 0.5%;
	font-weight: bolder;
	font-size: 15px;
	margin-left: 3px;
	margin-right: 3px;
	background: #ffffff;
	text-align: center;
	border-radius: 20px;
}

.button {
	display: block;
	width: 115px;
	height: 25px;
	background: #4e9caf;
	padding: 10px;
	text-align: center;
	border-radius: 5px;
	color: white;
	font-weight: bold;
}
.Other-Links:hover {
	color: #979a9a;
	text-decoration: none;
	text-align: center;
}

a:link,
a:visited,
a:active {
	color: DarkSlateGray;
	text-decoration: none;
	text-align: center;
	font-weight: bolder;
	font-size: 25px;
	padding-top: 10px;
	padding-right: 5px;
}

a:hover {
	color: #979a9a;
	text-decoration: none;
	text-align: center;
}
ul {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	list-style: none;
}
li {
	text-align: left;
	margin: 0.5rem;
	font-size: 1.1rem;
}

.fa {
	font-size: 200px;
	margin-left: 1rem;
	color: DarkSlateGray;
}

i {
	font-size: 100px;
	color: DarkSlateGray;
}

@media screen and (max-width: 400px) {
	.Profile-Main-Logo {
		height: 100px;
	}

	.Projects-Section {
		margin: 0;
		margin-top: 1rem;
		width: 90%;
	}
	.Project-detailed-info {
		font-size: 0.7rem;
		text-align: left;
	}
	.Project-details {
		font-size: 0.7rem;
		text-align: left;
	}
	.Projects-Description {
		display: grid;
		grid-template-columns: 1fr;
	}

	.Project-image {
		height: 0%;
		width: 0%;
	}
	.App-logo {
		height: 40px;
		float: left;
		padding: 0 15px 15px 10px;
	}
	.App-intro {
		font-size: 0.7rem;
	}
	.Profile-Name {
		width: 100%;
		font-size: 1rem;
	}
	.About-Me-Content {
		font-size: 0.7rem;
	}

	li {
		text-align: left;
		margin: 1rem;
		font-size: 0.7rem;
	}
	.Other-Links:link,
	.Other-Links:visited,
	.Other-Links:active {
		font-size: 0.7rem;
	}
}
@media screen and (max-width: 900px) and (min-width: 500px) {
	.Profile-Main-Logo {
		height: 100px;
	}

	.Projects-Section {
		margin: 0;
		margin-top: 1rem;
		width: 90%;
	}
	.Project-detailed-info {
		font-size: 1rem;
		text-align: left;
	}
	.Project-details {
		font-size: 1rem;
		text-align: left;
	}
	.Projects-Description {
		display: grid;
		grid-template-columns: 1fr;
	}

	.Project-image {
	}
	.App-logo {
		height: 40px;
		float: left;
		padding: 0 15px 15px 10px;
	}
	.App-intro {
		font-size: 1rem;
	}
	.Profile-Name {
		width: 100%;
		font-size: 1rem;
	}
	.About-Me-Content {
		font-size: 0.7rem;
	}

	li {
		text-align: left;
		margin: 1rem;
		font-size: 1rem;
	}
	.Other-Links:link,
	.Other-Links:visited,
	.Other-Links:active {
		font-size: 0.7rem;
	}
}



